import React from "react";
import Cube from "./Cube";
import ChartBar from "./ChartBar";
import ChartBarHorizontal from "./ChartBarHorizontal";
import IconChart from "./IconChart";

function Profile(props) {
  const [chartVisibility, setchartVisibility] = React.useState(true);

    const handleChart = () => {
    chartVisibility ? setchartVisibility(false) : setchartVisibility(true);
  };
  /* document.addEventListener('click', (e)=>{
  if(chartVisibility && e.target.className !== "profile_info" && e.target.className !== "profile_info-active"){
    setchartVisibility(!chartVisibility)
  }
}) */

  return (
    <div className="profile animate__animated animate__fadeInRight">
              <h1 id="profile_title"
          className={chartVisibility ? "profile_title" : "profile_title-active"}
        >
          {props.profile.title}
        </h1>

          <p className={chartVisibility ? "read-more_active" : "read-more"} onClick={handleChart}>read more</p>
        {/*<Cube />*/}

      <div className="charts">
        <ChartBar visibility={chartVisibility} />
        <ChartBarHorizontal visibility={chartVisibility} />

      </div>
      <article id="profile_info"
        className={chartVisibility ? "profile_info" : "profile_info-active"}
      >
          <span className="background_text">{props.profile.title}</span>

          {props.profile.about.map((arr, ind) => {
          return <p>{arr}</p>;
        })}
        <div className="icon_chart" onClick={handleChart}>
          <IconChart />
        </div>
      </article>
    </div>
  );
}

export default Profile;
